/* Generated by Font Squirrel (https://www.fontsquirrel.com) on August 18, 2016 */

@font-face {
    font-family: 'reenie_beanieregular';
    src: url('../fonts/ReenieBeanie/reeniebeanie-webfont.woff2') format('woff2'),
         url('../fonts/ReenieBeanie/reeniebeanie-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;

}
/* ===== NavBar section ===== */

/* Boostrap default NavBar - custom settings */
header {
  min-height: 60px;
  .navbar.navbar-custom {
    min-height: 60px;
    background-image: none;
    background-color: $navbackground;
    border: none;
    .navbar-brand {
      min-height: 60px;
      padding-top: 0;
    }
    .navbar-toggle {
      margin-top: 12px;
      margin-right: 30px;
    }
    // content for toggling styled
    .collapse.navbar-collapse {
      margin-top: 7px;
    }
    .navbar-nav {
      > .active {
        > a, > a:hover, > a:focus {
          background-color: $bghoverfocus;
          background-image: none;
          box-shadow: none;
          }
      }
      .dropdown-menu {
        background-image: none;
        background-color: $navbackground;
        > li > a {
          color: $navcolor;
          &:hover,  &:focus {
            background-color: $bghoverfocus;
            background-image: none;
            color: $activecolor;
          }
        } 
      }
    }
  }
}

// Expanded dropdown menu on mobile screens ('dropdown-menu' class removed in JS)
header {
    .navbar.navbar-custom {
      .nav.navbar-nav ul {
        > li {
          list-style-type: none;
          padding-top: 5px;
          padding-bottom: 5px;
          > a {
            text-decoration: none;
            color: $navcolor;
          }
        }
      }
    }
}

/* ===== Slider section ===== */

/* Boostrap Default Carousel - custom settings */
#mainCarousel {
	padding-top: 0;
	.carousel-indicators {
    position: relative;
    top: 5px;
		li {
			width: 12px;
			height: 12px;
			margin: 4px;
			&:active {
				width: 12px;
				height: 12px;
				margin: 4px;
			}
		}	
	}	
  .carousel-inner .item {
    img {
      width: 100%;
    }
    .carousel-caption {
      position: relative;
      left: 0;
      right: 0;
      padding-top: 0;
      padding-bottom: 0;
      margin-bottom: -5px;
    }
  } 
  //caption styling - extra div wrapping text content
  .carousel-descript {
    background-color: rgba(119, 19, 19, 0.7);
    padding: 2% 5% 0 5%;
    //caption min-height (for mobile, other screens min-height styled through mediaqueries)
    min-height: 140px;
    a {
      text-decoration: none;
      color: #ffffff;
    }
  }
}


/* ===== Boxes section ===== */

.boxes {
	font-family: 'Reenie Beanie', cursive;
  //margin below columns
  div[class^="col-"] {
    margin-bottom: 15px;
  }
  a {
    text-decoration: none;
  	.box-title {
  		position: relative;
  		bottom: 4rem;
  		text-align: right;
  		color: #ffffff;
  		font-size: 2.1rem;
  		line-height: 0;
  		padding-right: 3rem;
  	}
    //black color only for boxes with white background
  	.box-title.font-black {
  		color: black;
  	}  
    img {
      width: 100%;
    }
  } 
}

/* ===== Footer section ===== */

footer {
  hr {
    border-color: $footcolor;
  }
  p {
    line-height: 3;
    color: $footcolor;
    text-align: center;
  }
  ul {
    list-style-type: none;
    text-align: center;
      li {
        display: inline-block;
        vertical-align: top;
        a {
        text-decoration: none;
        color: $footcolor; 
        .fa, span {
          font-size: 3rem;
        }
        .fa.fa-twitter {
          padding-left: 2rem;
        }
        .fa.fa-facebook-square {
          padding-right: 2rem;
        }
      }
    }
  }
}


